.navbar{display:flex;justify-content:space-between;padding:1rem;background-color:var(--bg);color:var(--text);transition:background-color .3s,color .3s}.button{padding:.5rem 1rem;border:1px solid var(--text);border-radius:8px;cursor:pointer;transition:background-color .3s,color .3s}.button:hover{background-color:var(--text);color:var(--bg)}